@import "../../../css-config/mixins.scss";

.services{
    padding: 60px 0;
    font-family: 'Montserrat', sans-serif;
    color: #767676;
    font-weight: 400;
    background-color: #f7f7f7;
    box-sizing: border-box;


.container{
    max-width: 1200px;
    width: 100%;
    padding: 0 20px;
    margin: 0 auto;


 h2{
    font-family: 'Montserrat', sans-serif;
    position: relative;
    font-size: 34x;
    line-height: 1.4;
    text-align: center;
    font-weight: 700;
    color: #343434;
    margin-bottom: 45px;

    @include md{
        
            font-size: 38px;
            margin-bottom: 60px;
      
    }

   
}



.cards{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 60px;
    
    @include md{

        grid-template-columns: repeat(3, 1fr);
        gap:100px

    }

    @include cardmd{
        grid-template-columns: repeat(1,1fr);
        gap: 50px;

    }


    .card{
        width: 100%;
        text-align: center;
        margin-bottom: 45px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;


       


        @include md{
            margin-bottom: 0;
            width: 98%;
          
            
            transition: width 1s;
            

          
        
             &:hover{
          width: 100%;
          border-bottom: 1px solid currentColor; 
            
             }
                  
              
        

        }


        svg{

            font-size: 50px;
    color: #cc4277;
    margin-bottom: 10px;

    @include cardmd{
        font-size: 70px;
    }
   

        }


        h3{
            font-size: 15px;
            line-height: 1.4;
            font-weight: 700;
            color: #343434;
            margin-bottom: 20px;
        }
    }















 
   
}


}
}

