@import "../../../css-config/mixins.scss";
.contact-infi{
color: black;
    height: auto;
.main-contact {
    // background-color: rgb(0, 174, 255);
    display: flex;
    justify-content: center;
    align-items: center;

    .contact {
        margin: 20px;
        padding: 26px 0;
        max-height: fit-content;
        width: fit-content;
        padding: 10px;
        border-radius: 110px 110px 30px 10px;
        border: 0.5px solid rgba(0, 0, 0, 0.199);

        background-color:transparent;
        box-shadow: 0 0px 18px 0 rgba(243, 238, 238, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

        color: black;

        .heading {
            padding: 20px 0;
            text-align: center;
            font-size: large;

            span {
                position: relative;
                top: 13px
            }
        }

        .content {


            margin: 50px;
            display: flex;
            flex-flow: wrap;
            justify-content: center;
            align-items: center;
            gap: 60px;
            font-size: 18px;
            
            cursor: pointer;

            .sub-content {
                padding: 8px 31px;



               
               


            }

            #mail{
               
                    background-color: rgb(5, 118, 188);
                    border-radius: 20px;
        
             
            }
           


        }
       

        .page{
            display: flex;
            justify-content: center;
            justify-content: center;
            
        }

    }

   
}

}