.sp-me{
    
    margin: 40px auto;
    padding: 30px 0;

    h2{
        text-align: center;
        font-size: 24px;
        line-height: 30px;
        margin-bottom: 30px;
    }

.sp-main {
    display: grid;
    grid-template-columns: auto auto auto;
    
   row-gap: 40px;
    justify-content: space-around;
    align-items: center;
    padding: 20px 10px;

    span {
       
        transition: all 0.5s;
       
       
        
        

        &:hover {
            transform: translateY(-10px);
        }

        svg {
            font-size: 44px;
            color: rgb(0, 0, 0);
            
           
        }
    }
}
}
