@import "../../css-config/mixins.scss";

.main-header{
    width: 100%;
    padding: 0 20px;
    background-color: white;
    color: white;
    border-bottom: 1px solid rgba(0,0,0,0.1);
    z-index: 99; 
    
    @include md{
        padding: 0 40px;
    }
    
    .header-content{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 50px;
        max-width: 1200px;
        margin: 0 auto;
        @include md {
            height: 80px;
        }
        .left{
            list-style-type: none;
            color: grey;
            display: none;
            gap:25px;
            text-decoration: none;
            @include md{
                display: flex;
                

            }

       

            .icon-part{
                display: flex;
                justify-content: center;
                align-items: center;
                gap: 5px;
                flex-direction: column;
                transition: all ease-in;
                animation: 0.2s;
                color: grey;

                @include md{
                    &:hover{
                        transform: scale(0.9);
                        color: rgb(0, 0, 0);
                    }
                }

               
            }
            li{
                font-size: 14px;
                font-weight: 600;
                text-transform:uppercase;
                cursor: pointer;

            };
           


        }

        .center{
            
            height: auto;
            width: 150px;
            cursor: pointer;

            img{
                height: 100%;
                width: 100%;
            }
            @include md{
                width: 200px;
                height: auto;
               
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
            }

            @include ssss{
width: 90px;
height: auto;
            }

        }

        .right{
            display: flex;
            align-items: center;
            gap: 17px;
            @include md{
                gap:25px
            }

            svg{
                transform: scale(1.05);
                cursor: pointer;
                font-weight: 700;
                color: grey;
                transition: all ease-in;
                animation: 0.2s;
               
                @include md{
                    transform: scale(1.2);

                    &:hover{
                        transform: scale(1.23);
                        color: rgb(2, 2, 2);
                    }
                }
            }
            .cart-icon{
                position: relative;
                span{
                    min-width: 20px;
                    text-align: center;
                    background-color: #8e2de2;
                    padding: 2.5px;
                    position: absolute;
                    top: -5px;
                    right: -12px;
                    font-size: 12px;
                    line-height: 1;
                    border-radius: 10px;
                }
            }

        }
    }
    a{
        text-decoration: none;
        color: white;
    }

    &.sticky-header{
        position: sticky;
        top: 0;
       
        animation: stickyHeader 0.3s ease forwards;
        width: 100%;
        padding: 0 20px;
        background-color: rgb(255, 255, 255);
       
        border-bottom: 1px solid rgba(0,0,0,0.1);
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
        z-index: 99;
        
        .left{
            list-style-type: none;
            color: grey;
            display: none;
            gap:25px;
            text-decoration: none;
            @include md{
                display: flex;
                

            }

       

            .icon-part{
                display: flex;
                justify-content: center;
                align-items: center;
                gap: 5px;
                flex-direction: column;
                transition: all ease-in;
                animation: 0.2s;
                color: rgb(23, 23, 23);

                @include md{
                    &:hover{
                        transform: scale(0.9);
                        color: rgb(0, 0, 0);
                    }
                }

               
            }
            li{
                font-size: 14px;
                font-weight: 600;
                text-transform:uppercase;
                cursor: pointer;

            };
           


        }

        .right{
            display: flex;
            align-items: center;
            gap: 17px;
            @include md{
                gap:25px
            }

            svg{
                transform: scale(1.05);
                cursor: pointer;
                font-weight: 700;
                color: rgb(21, 21, 21);
                transition: all ease-in;
                animation: 0.2s;
               
                @include md{
                    transform: scale(1.2);

                    &:hover{
                        transform: scale(1.23);
                        color: rgb(2, 2, 2);
                    }
                }
            }
            .cart-icon{
                position: relative;
                span{
                    min-width: 20px;
                    text-align: center;
                    background-color: #8e2de2;
                    padding: 2.5px;
                    position: absolute;
                    top: -5px;
                    right: -12px;
                    font-size: 12px;
                    line-height: 1;
                    border-radius: 10px;
                }
            }

        }

       
        
    }
}

@keyframes stickyHeader{
    0% {transform:translateY(-80px);}
    100%{transform: translateY(0);}
}