@import "../../css-config/mixins.scss";

.web-batch{


    // padding: 20px 0;

    @include md{
        // padding: 40px 0;
    }

    .join-now-main{
        background-image: repeating-radial-gradient(rgba(23, 129, 221, 0.575),rgba(13, 13, 164, 0.443));
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 25px;
        text-transform: uppercase;
        padding: 30px 0;
        width: 100%;

        @include md{
            gap: 40px;
        }

       h1{
        color: rgba(0,0,0,0.8);
        font-size: 18px;
        word-spacing: 7px;
        text-align: center;

        @include md{
            font-size: 29px;
        }

        span{
            color: rgb(239, 239, 239);
            text-shadow: 0 0 3px #FF0000, 0 0 5px #0000FF;

        }
       }

       h3{
        font-weight: 900;
        letter-spacing: 2px;
        font-size: 15px;
        

        @include md{
            font-size: 22px;
        }
       }

       .btn{
        padding: 12px 16px;
        font-weight: 900;
        letter-spacing: 2px;
        background-color: rgba(46, 20, 20, 0.511);
        border-radius: 15px;
       }
    }

    .link-program{
        display: flex;
        margin: 40px 0;
        justify-content: space-around;
        align-items: center;
        flex-flow: wrap;
        line-height: 40px;
        gap: 20px;
        padding: 20px 10px;
        font-size: 20px;
        font-family:Arial, Helvetica, sans-serif;

        a{
            text-decoration: underline;
            cursor: pointer;

            &:hover{
                color: blue;
            }
        }
    }

    .why{
        margin: 40px 0;
        padding: 0 10px;

        @include md{
            padding: 0 20px;
            margin: 80px 0;
        }


        h1{
            font-size: 20px;
           
            color: rgba(0,0,0,0.8);
            text-transform: capitalize;
            &:after {
                content: "";
                display: block;
                margin-top: 8px;
                width: 40px;
                height: 3px;
                background-color: #8e2de2;
    
                @include md {
                    margin-top: 15px;
                    width: 70px;
                    
                }
            }

            @include md{
                font-size: 28px;
            }
        }

        .why-card{
           display: flex;
           flex-direction: column;
            margin: 30px 0;
            padding: 0;

            @include md{
                padding: 0 50px;
            }

            .card{
                box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
                padding: 20px;
               width: fit-content;
               background-color: white;
               border-radius: 10px;
               line-height: 30px;
               font-family:Arial, Helvetica, sans-serif;
               
               
              
   
            }  

        }
       
    }

    .batch-topics{
        margin: 80px 0;
        padding: 0 10px;

        @include md{
            padding: 0 20px;
            margin: 80px 0;
          
        }


        h1{
            font-size: 20px;
            font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
           
            color: rgba(0,0,0,0.9);
            text-transform: capitalize;
            &:after {
                content: "";
                display: block;
                margin-top: 8px;
                width: 40px;
                height: 3px;
                background-color: #8e2de2;
    
                @include md {
                    margin-top: 15px;
                    width: 70px;
                    
                }
            }

            @include md{
                font-size: 22px;
            }
        }

        .list-topics{
            display: grid;
            grid-template-columns: auto auto;
            padding: 40px 0px;
            justify-content: flex-start;

            @include md{
                grid-template-columns: auto auto auto auto auto;
                padding: 40px 50px;
               

            }

            @include ssms{
                grid-template-columns: auto auto auto;
              
            }

            @include cd{
                grid-template-columns: auto auto;
                justify-content: center;
          
              
                }
            
         
           
           
            

            .list{
                display: flex;
                align-items: center;
                letter-spacing: 2px;
                font-weight: 800;
                color: rgba(0, 0, 0, 0.8);
                padding: 10px 10px;
                font-size: 13px;

                @include md{
                    font-size: 16px;
                    padding: 10px 30px;
                }
                
            }
        }

      
       
    }


    .certi-batch{
        h1{
            font-size: 20px;
            font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
           
            color: rgba(0,0,0,0.9);
           text-align: center;
            text-transform: capitalize;
        

            @include md{
                font-size: 22px;
            }
        }

        h3{
            font-size: 18px;
            font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
           
            color: rgba(0,0,0,0.9);
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 20px 0;
           text-align: center;
            text-transform: capitalize;
            gap: 12px;
        

            @include md{
                font-size: 20px;
            }

            svg{
                color: rgb(64, 12, 147);
                font-size: 26px;
            }

            span{
                background-color: #0000ffc1;
                padding: 8px 16px;
                border-radius: 10px;
                color: white;
                cursor: pointer;
            }
        }
    }


    .who{
        margin: 40px 0;
        padding: 20px 10px;
        background-image: url("../../assets/remove.png");
        background-size: 50%;

        @include md{
            padding: 30px 20px;
            margin: 80px 0;
          
        }


      


        h1{
            font-size: 20px;
            text-align: center;
          
            font-weight: 900;
            margin: 30px auto;
            text-transform: uppercase;
            font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
           
            color: rgba(0,0,0,0.9);
            text-transform: capitalize;
            padding: 8px 8px;
            border-radius: 10px;
            // text-shadow: 0 0 3px #e9e9e9, 0 0 5px #e8e8e8;
            background-color: white;
            width: fit-content;
           
            line-height: 40px;

            @include md{
                font-size: 22px;
            }

            span{
                // text-shadow: 0 0 3px rgba(15, 151, 219, 0.502), 0 0 5px rgb(235, 235, 243);
                text-align: center;
                
                font-weight: 900;
                padding: 5px 0;
                line-height: 40px;
                color: rgb(2, 2, 2);
                font-size: 16px;
               
                font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
                @include md{
                    font-size: 20px;
                }
           
            }
    
        }

      
      

      
      
       
    }


}

