 @import "../../../css-config/mixins.scss";

 .navbar {
     overflow: hidden;
     background-color: #333;
    
    //  margin: 20px 0;
     display: none;

     @include md{
       
        display: flex;
        justify-content: center;
        gap: 10px;
     }
   
    
   
     




     .dropdown {
         float: left;
         overflow: hidden;
      
      


         .dropbtn {
             font-size: 16px;
             border: none;
             outline: none;
             color: white;
             padding: 14px 23px;
             background-color: inherit;
             font-family: inherit;
             margin: 0;
             display: flex;
             justify-content: center;
             align-items: center;
             gap: 10px;
             transition: all 0.6s;
         }

        
         &:hover .dropbtn {
            //  background-color: rgb(15, 255, 91);
            //  color: black;
            //  font-weight: 500;
         
                
          
          
                color: black;
                
                background: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
             
         }

         .dropdown-content {
             display: none;
             position: absolute;
             background-image: linear-gradient(to left,rgba(255, 255, 255, 0.868),rgba(255, 255, 255, 0.973));
             min-width: 160px;
             animation: dropdownOpen 0.3s ease-in-out;
               box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
             z-index:99;
           
             

             a {
                float: none;
                transition: all 0.2s ease-in-out;
                padding: 12px 16px;
                text-decoration: none;
                display: block;
                text-align: left;
                display: flex;
                // justify-content: center;
                align-items: center;
                gap: 20px;
                // cursor: pointer;
                border-bottom: 1px solid rgba(0,0,0,0.5);
                font-weight: 600;
                color: rgb(0, 0, 0);
                pointer-events: visible; /* Disable pointer events to make it unclickable */
                text-decoration: none; /* Remove underline */
                cursor: pointer;
                
                

                &:hover {
                   color: rgb(0, 0, 0);
              text-decoration: underline;
              
              
             
                  border-right: 4px solid rgba(33, 33, 34, 0.808);
        // box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
                }
            }
         }

         

         

         &:hover .dropdown-content {
             display: block;
             
           
        
         }
     }
 }

 @keyframes dropdownOpen {
    0% {
      transform: scaleY(0)
      
    }
    100% {
    transform: scaleY(1);
    }
  }