@import "../../../../css-config/mixins.scss";

.office{
    // margin: 40px 0;
    text-align: center;
    line-height: 30px;

    @include md{
        // margin: 50px 0;
    }

    h1{
        font-size:x-large;
      line-height: 40px;
      padding: 30px 0;
      color: black;
    }


    .office-card{
        margin: 40px 0;
        display: flex;
        justify-content: center;
        gap: 50px;
        flex-flow: wrap;

        .office-card-first{
            height: fit-content;
color: black;
           border: 0.5px solid rgba(0, 0, 0, 0.199);
            background-color: aliceblue;
            width: 300px;
            padding: 10px;
            box-shadow: 0 0px 8px 0 rgba(243, 238, 238, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            border-radius: 20px;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            gap: 20px;

            p{
                word-spacing: 2px;
            }
        }
    }
}