// Creative.scss
@import "../../../css-config/mixins.scss";
.creative-container-unic {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
   
    flex-wrap: wrap;
    max-width: 1000px; 
   
    margin: 40px auto; // Center the container horizontally
    gap: 20px;
    padding: 10px;
    display: none;

  
    @include md {
      gap: 50px;
      flex-wrap: nowrap;
      display: flex;
    }
 
  
  .box-unic {
    width: 100%; 
    margin: 0 auto;
    height: 200px; 
    background-color: #f7f7f7;
    color: #767676;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    transition: background-color 0.3s, color 0.3s;
  
    @include md {
      width: 50%;
      padding: 10px;
  
       
      &:nth-child(1){
        display: flex;
      flex-direction: column;
      line-height: 40px;
      justify-content: space-around;
      transition: width 1s;

      &:hover{
        width: 80%;
        color: black;
                
        background-color:transparent;
box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
      }

      h2{
        cursor:default;
        letter-spacing: 4px;
        font-family: monospace;
        font-size: larger;
      
        padding: 5px 10px;
        display: flex;
        gap: 10px;


        span{
          display: none;
         text-decoration: none;
        }
      
        &:hover{
          text-decoration: underline 1px rgb(12, 162, 243);
       
        }
      }
      }

      &:nth-child(2){
display: flex;
flex-direction: column;
background-color: #cc4277;
justify-content: space-around;
line-height: 40px;
color: #f7f7f7;

&:hover{
  color: black;
                
  background-color:transparent;
box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}




        .box1{
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          text-align: center;
          font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
        }
        .box2{
         
          flex-direction: column;
          justify-content: space-around;
          display: none;
         text-align: center;
        }
      }
  
     
    
    }
  }

}
  