@import "../../../css-config/mixins.scss";
.hero {
    background: linear-gradient(100deg, rgba(10, 10, 10, 0.32) 38%, rgba(255, 253, 253, 0.324) 49%,  rgba(0, 0, 0, 0.365) 44%,);
    color: white;
    text-align: center;
    padding: 4rem 0;
    



    @include md{
      background: linear-gradient(50deg, rgba(10, 10, 10, 0.342) 38%, rgba(255, 253, 253, 0.324) 49%,  rgba(0, 0, 0, 0.105) 44%,);
    }

   
  
    .container {
      max-width: 1200px;
      margin: 0 auto;
      gap: 10px;
      display: flex;
      flex-direction: column;
      align-items: center;
  
      .hero-image {
        width: 100%;
        // display: none;
        -webkit-user-drag: none;
        pointer-events: none;
  user-select: none;
        max-width: 600px;
        height: auto;
        margin-bottom: 2rem;
      }
  
      .hero-title {
        font-size: 2.5rem;
        font-weight: bold;
        margin-bottom: 1rem;
        text-transform: uppercase;

        span{
            color: #000000;
        // animation: changeColor 2s infinite;
        }
      }
  
      @keyframes changeColor {
        0% {
          background-color: white;
          color: black;
        }
        50% {
          background-color:transparent; 
          color: white;
        //   color: white;/* Change to your desired color */
        border: 1px solid white;
        }
        100% {
          background-color: white;
          color: black;
        }
      }
  
      .hero-buttons {
        display: flex;
        flex-flow: wrap;
       row-gap: 10px;
        justify-content: center;
  
        .hero-button {
          
          background-color: transparent;
          color: white;
          font-weight: bold;
          padding: 1rem 2rem;
          margin: 0 0.5rem;
          border-radius: 25px;
        //   border: 1px solid white;
          text-transform: uppercase;
          cursor: pointer;
          animation: changeColor 5s infinite;
          transition: background-color 0.8s;
  
          &:nth-child(1) {
            background-color: white;
            color: #333;
            animation: none;
  
            &:hover {
              border: 1px solid white;
              color: white;
              background-color: transparent;
            }
          }
  
          &:hover {
            border: 1px solid white;
            animation: none;
          }
        }
      }
    }
  
    @media (min-width: 768px) {
      .container {
        flex-direction: row-reverse;
  

        .hero-title {
          font-size: 3rem;
        }

        .hero-image{
          display: block;
        }
  
        .hero-subtitle {
          font-size: 1.75rem;
        }
      }
    }
  }
  